@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.terminal {
    background-color: black;
}
.name {
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    white-space: nowrap;
}
.name i {
    font-size: 7.5vw;
    padding: 3vh;
    color: rgb(75, 75, 75);
}
.name b {
    font-size: 5vw;
    padding: 3vh;
    color: white;
    font-family: 'Source Code Pro', monospace;
}  

@media only screen and (max-width: 600px) {
    .name i {
        font-size: 15vw;
        padding: 3vh;
        color: rgb(75, 75, 75);
    }
    .name b {
        font-size: 12vw;
        padding: 3vh;
        color: white;
        font-family: 'Source Code Pro', monospace;
    }
}

.Footer a {
    padding: 3vw;
}
.fa-envelope {
    color: rgb(212, 70, 56);
}
.fa-github {
    color: rgb(60, 60, 60);
}
.fa-linkedin {
    color: #0e76a8;
}
.fa-twitter {
    color:  #00acee;
}
.fa-instagram {
    color: #dd2a7b;
}
.fa-telegram {
    color: rgb(0, 136, 204);
}

